<template>
    <div class="box">
        <div class="header">
            <div v-lazy:background-image="require('../../../assets/can-join.png')">
                <div>
                    <self-icon class="icon" type="can-join"></self-icon>
                    <span>{{ $t('message.canJoin') }}</span>
                </div>
                <span>{{ canJoin }}ETH</span>
            </div>
            <div v-lazy:background-image="require('../../../assets/current-join.png')">
                <div>
                    <self-icon class="icon" type="outline"></self-icon>
                    <span>{{ $t('message.currentJoin') }}</span>
                </div>
                <span>{{ currentJoin }}ETH</span>
            </div>
            <div v-lazy:background-image="require('../../../assets/total-join.png')">
                <div>
                    <self-icon class="icon" type="total-join"></self-icon>
                    <span>{{ $t('message.totalJoin') }}</span>
                </div>
                <span>{{ totalJoin }}ETH</span>
            </div>
        </div>
        <div class="content">
            <div class="title">
                <i></i>
                <span>{{ $t('message.joinQuota') }}</span>
            </div>
            <div class="input">
                <input type="number" placeholder="0" v-model.number="amount">
                <span>ETH</span>
            </div>
            <div class="amount">
                <div>
                    <img v-lazy="require('../../../assets/eth.png')"/>
                    <span>{{ ethBalance }} ETH</span>
                </div>
                <div>
                    <img v-lazy="require('../../../assets/join-sfc.png')"/>
                    <span>{{ sfcBalance }} SFC</span>
                </div>
            </div>
            <div class="select-join">
                <Button :disabled="oneETH" @click="amount = 1">1ETH</Button>
                <Button :disabled="fiveETH" @click="amount = 10">10ETH</Button>
                <Button :disabled="tenETH" @click="amount = 30">30ETH</Button>
                <Button :disabled="twentyETH" @click="amount = 50">50ETH</Button>
            </div>
            <div class="consume">
                <div>
                    <div>
                        <i></i>
                        <span>{{ $t('message.gasPrice') }}</span>
                    </div>
                    <span>{{ Number(amount) * changeProp * gasProp }} SFC</span>
                </div>
                <div>
                    <div>
                        <i></i>
                        <span>{{ $t('message.outQuota') }}</span>
                    </div>
                    <span>{{ outAmount }} ETH</span>
                </div>
            </div>
        </div>
        <div class="footer">
            <Button @click="join" :disabled="!checked">{{ $t('message.define') }}</Button>
            <checkbox class="checkbox" v-model="checked">
                {{ $t('message.joinRule1') }}<span style="color: #4D95FC;" @click="rule = true">{{ $t('message.joinRule2') }}</span>{{ $t('message.joinRule3') }}
            </checkbox>
        </div>
        <Overlay :show="show" @click="show = false">
            <div class="overlay" @click.stop v-lazy:background-image="require('../../../assets/limit-bg.png')">
                <img v-lazy="require('../../../assets/limit-close.png')" @click="show = false" />
            </div>
        </Overlay>
        <Overlay :show="rule" @click="rule = false">
            <div v-if="$i18n.locale === 'zh-CN'" class="rule" @click.stop>
                <span></span>
                <div>
                    Smurf<br />
                    
                    <p>1.</p>
                    <p>2.</p>
                    <p>3.</p>
                    <p>4.</p>
                    <p>5.</p>
                    
                    <p>1.</p>
                    <p>2.100% ETH+1/20ETH SFC</p>
                    <p>3.+</p>
                    <p>4.830 ETH5</p>
                    <p>5.3630011002-657-3003</p>
                </div>
                <div>
                    <span>{{ $t('message.redyRule') }}</span>
                    <Button @click="rule = false">{{ $t('message.agree') }}</Button>
                </div>
            </div>
            <div v-else class="rule" @click.stop>
                <span>Participation rules</span>
                <div>
                    Smurf is a decentralized financial management project with uncertain risk factors. Please participate rationally after you are familiar with the following rules:<br />
                    1Smurfs risk guidelines:
                    <p>Do not make any form of commitment and guarantee to others;</p>
                    <p>Don't exaggerate to others;</p>
                    <p>Do not collect any member's funds;</p>
                    <p>Do not use any special agreement to attract others;</p>
                    <p>You can share with others only after you fully understand all the system modes of Smurf.</p>
                    2Smurf revenue rule:
                    <p>The static income ratio changes every day, which is determined by the built-in algorithm and full intelligence;</p>
                    <p>Participation mode: 100% ETH+SFC of 1 / 20 ETH;</p>
                    <p>After the static income + management income reaches the exit quota, it needs to participate again to continue to enjoy the relevant income;</p>
                    <p>The Day prize starts at 8 a.m. every day, and 5 players participating in 30 ETH and above are randomly selected for average distribution;</p>
                    <p>After 36 hours of countdown to the future awards, the awards will be given to the last 300 players, the first to last: 100 times, the second to sixth to last: 5 times, and the seventh to 300 to last: 3 times.</p>
                </div>
                <div>
                    <span>{{ $t('message.redyRule') }}</span>
                    <Button @click="rule = false">{{ $t('message.agree') }}</Button>
                </div>
            </div>
        </Overlay>
    </div>
</template>

<script>
import { Checkbox, Overlay, Button, Toast } from 'vant'
let from = '/'

export default {
    components: { Checkbox, Overlay, Button },
    data() {
        return {
            canJoin: 0,
            currentJoin: 0,
            totalJoin: 0,
            ethBalance: '0.00',
            sfcBalance: '0.00',
            amount: '',
            changeProp: 3000,
            gasProp: 0.05,
            outAmount: '0.00',
            allCanJoin: 0,
            oneETH: true,
            fiveETH: true,
            tenETH: true,
            twentyETH: true,
            show: false,
            rule: false,
            checked: false,
            minJoin: 0,
            maxJoin: 0
        }
    },
    methods: {
        join() {
            let amount = Number(this.amount),
                gasPrice = amount * this.changeProp * this.gasProp

            if (amount < this.minJoin || amount > this.maxJoin) {
                // return Toast.fail(`${this.$t('message.minJoin')}${this.minJoin}ETH${this.$t('message.maxJoin')}${this.maxJoin}ETH`)
                return Toast.fail(`${this.$t('message.minJoin')}1ETH${this.$t('message.maxJoin')}50ETH`)
            } else if (amount > this.canJoin && this.currentJoin > 0) {
                return Toast.fail(this.$t('message.notJoinRange'))
            } else if (amount > this.allCanJoin) {
                return this.show = true
            } else if (gasPrice > this.sfcBalance) {
                return Toast.fail(this.$t('message.notSFC'))
            }

            this.$contract.round.methods.Join().send({
                from: this.$address,
                value: this.$toWei(`${amount}`)
            }).on('transactionHash' , txHash =>{
                console.log(txHash)
            }).on('error' , console.error)
            .on('receipt' , result=>{
                console.log(result)
                this.getData()
                if (from === '/') {
                    this.$router.back()
                } else {
                    this.$router.back(-2)
                }
            })
        },
        init() {
            if (this.$cache.join) {
                this.canJoin = this.$cache.join.canJoin
                this.currentJoin = this.$cache.join.currentJoin
                this.totalJoin = this.$cache.join.totalJoin
                this.outAmount = this.$cache.join.outAmount
                this.ethBalance = this.$cache.join.ethBalance
                this.sfcBalance = this.$cache.join.sfcBalance
                this.allCanJoin = this.$cache.join.allCanJoin
                this.minJoin = this.$cache.join.minJoin
                this.maxJoin = this.$cache.join.maxJoin
            } else {
                this.$cache.join = {}
            }

            this.getData()
            this.isShowETH()
        },
        isShowETH() {
            if (this.canJoin >= 50 || this.currentJoin == 0) {
                this.oneETH = false
                this.fiveETH = false
                this.tenETH = false
                this.twentyETH = false
            } else if (this.canJoin >= 30) {
                this.oneETH = false
                this.fiveETH = false
                this.tenETH = false
            } else if (this.canJoin >= 10) {
                this.oneETH = false
                this.fiveETH = false
            } else if (this.canJoin >= 1) {
                this.oneETH = false
            }
        },
        getData() {
            this.$contract.round.methods._depositMinLimit().call((err, res) => {
                if (!err) {
                    this.minJoin = this.$fromWei(res)
                    this.$cache.join.minJoin = this.minJoin
                }
            })

            this.$contract.round.methods._depositMaxLimit().call((err, res) => {
                if (!err) {
                    this.maxJoin = this.$fromWei(res)
                    this.$cache.join.minJoin = this.maxJoin
                }
            })

            this.$contract.round.methods.GetCurrentGameStatus()
            .call({ from: this.$address }, (err, res) => {
                if (!err) {
                    this.currentJoin = this.$fromWei(res.currentEther)
                    this.canJoin = this.currentJoin > 0 ? this.$fromWei(res.depositMaxLimit) - this.currentJoin : 50
                    this.$cache.join.canJoin = this.canJoin
                    this.$cache.join.currentJoin = this.currentJoin

                    let outAmount = this.currentJoin * Number(res.profixMultiplier) - this.$fromWei(res.withdrawableTotal)
                    this.outAmount = outAmount.toFixed(2)
                    this.$cache.join.outAmount = outAmount.toFixed(2)
                }
            })

            this.$contract.round.methods.TotalInOutAmount()
            .call({ from: this.$address }, (err, res) => {
                if (!err) {
                    this.totalJoin = this.$fromWei(res.inEther)
                    this.$cache.join.totalJoin = this.totalJoin
                }
            })

            this.$web3.eth.getBalance(this.$address).then(balance => {
                this.ethBalance = this.$fromWei(balance, 2)
                this.$cache.join.ethBalance = this.ethBalance
            })

            this.$contract.erc20token.methods.balanceOf(this.$address).call((err, res) => {
                if (!err) {
                    this.sfcBalance = this.$fromWei(res, 2)
                    this.$cache.join.sfcBalance = this.sfcBalance
                }
            })

            this.$contract.cost.methods.CurrentCostProp().call((err, res) => {
                if (!err) {
                    this.changeProp = this.$fromWei(res)
                }
            })

            this.$contract.cost.methods._costProp().call((err, res) => {
                if (!err) {
                    this.gasProp = Number(res) / 100
                }
            })

            this.$contract.turing.methods.GetDepositedLimitCurrentDelta().call((err, res) => {
                if (!err) {
                    this.allCanJoin = this.$fromWei(res)
                    this.$cache.join.allCanJoin = this.allCanJoin
                }
            })
        }
    },
    mounted() {
        let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale
        this.init()
    },
    beforeRouteEnter(to, from, next) {
        from = from.path
        next()
    }
}
</script>

