<template>
    <div class="van-tabbar-page box">
        <img src="../../assets/My-Logo.png" />
        <div class="myCode">
            <router-link to="/my/view/BuyRating">
                <img :src="require(`../../assets/v${grade}.png`)" />
            </router-link>
            <div>
                <span>{{ $t('message.inviteCode') }}</span>
                <p>{{ invitationCode }}</p>
            </div>
            <self-icon
              type="copy"
              v-clipboard:copy="invitationCode"
              v-clipboard:success="onCopySuccess"
              v-clipboard:error="onCopyError"
            ></self-icon>
        </div>

        <div class="myContent">
            <div class="myContentList">
                <div>
                    <self-icon type="href"></self-icon>
                    <span>{{ $t('message.inviteLink') }}</span>
                </div>
                <div
                  v-clipboard:copy="invitationLink"
                  v-clipboard:success="onCopySuccess"
                  v-clipboard:error="onCopyError"
                  v-if="isBind"
                >{{ $t('message.copy') }}</div>
                <span v-else style="color: #666" @click="goBind">{{ $t('message.copy') }}</span>
            </div>
            <div class="myContentList">
                <div>
                    <self-icon type="inviteList"></self-icon>
                    <span>{{ $t('message.inviteList') }}</span>
                </div>
                <router-link to="/my/view/myLnvitationList" v-if="isBind">{{ $t('message.look') }}</router-link>
                <span v-else style="color: #666" @click="goBind">{{ $t('message.look') }}</span>
            </div>
            <div class="myContentList">
                <div>
                    <self-icon type="Superior"></self-icon>
                    <span>{{ $t('message.myInvite') }}</span>
                </div>
                <span v-if="!showInviter">******</span>
                <span v-else>{{ myInviter }}</span>
                <div @click="showInviter = !showInviter" v-if="isBind">
                    <self-icon type="eye"></self-icon>
                    <div class="line" v-if="!showInviter"></div>
                </div>
                <div v-else @click="goBind">
                    <self-icon type="eye" style="color: #666"></self-icon>
                    <div class="line" style="background-color: #666"></div>
                </div>
            </div>
            <div class="myContentList">
                <div>
                    <self-icon type="receive"></self-icon>
                    <span>{{ $t('message.drawSFC') }}</span>
                </div>
                <router-link to="/compensate" v-if="isBind">{{ $t('message.draw') }}</router-link>
                <span v-else style="color: #666" @click="goBind">{{ $t('message.draw') }}</span>
            </div>
            <div class="myContentList">
                <div>
                    <self-icon type="Node"></self-icon>
                    <span>{{ $t('message.allianceNode') }}</span>
                </div>
                <router-link :to="path" v-if="isBind">{{ $t('message.enter') }}</router-link>
                <span v-else style="color: #666" @click="goBind">{{ $t('message.enter') }}</span>
            </div>
            <div class="myContentList">
                <div>
                    <self-icon type="manager"></self-icon>
                    <span>{{ $t('message.manager') }}</span>
                </div>
                <router-link to="/my/view/managerAssets" v-if="isBind">{{ $t('message.draw') }}</router-link>
                <span v-else style="color: #666" @click="goBind">{{ $t('message.draw') }}</span>
            </div>
            <div class="myContentList">
                <div>
                    <self-icon type="exit"></self-icon>
                    <span>{{ $t('message.exit') }}</span>
                </div>
                <router-link to="/my/view/exit" v-if="isBind">
                    <self-icon type="next"></self-icon>
                </router-link>
                <self-icon v-else type="next" style="color: #666" @click="goBind"></self-icon>
            </div>
        </div>
        <Overlay :show="show" @click="show = false">
            <div class="grade-bg">
                <img v-lazy="require('../../assets/update-grade.png')" />
                <span>V{{ updateGrade }}</span>
            </div>
            <Button class="grade-btn" @click="upgrade">{{ $t('message.upgrade') }}</Button>
        </Overlay>
    </div>
</template>

<script>
import { Toast, Overlay, Button, Dialog } from "vant"
import { guide  } from '../../static/initData'

export default {
    name: "my",
    components: { Overlay, Button },
    data() {
        return {
            grade: 0,
            show: false,
            updateGrade: 0,
            invitationCode: "******",
            invitationLink: "",
            myInviter: "******",
            showInviter: false,
            path: "/my/view/JoinNode",
            isBind: localStorage.getItem('isBind') === 'true'
        };
    },
    methods: {
		goBind() {
			Dialog.alert({
				message: '!'
			}).then(() => {
				this.$router.push({ path: '/' })
			})
		},
        upgrade() {
            this.$contract.levelSub.methods.DoUpgradeLv().send({
                from: this.$address
            }).on("transactionHash", txHash => {
                console.log(txHash)
            }).on("error", console.error)
            .on("receipt", res => {
                console.log(res)
                this.show = false
                this.getData()
            })
        },
        onCopySuccess() {
            Toast.success(this.$t('message.copySuccess'))
        },
        onCopyError() {
            Toast.fail(this.$t('message.copyFail'))
        },
        init() {
            if (this.$cache.my) {
                this.path = this.$cache.my.path
                this.grade = this.$cache.my.grade
                if (this.$cache.my.invitationCode) {
                    this.invitationCode = this.$cache.my.invitationCode
                    this.invitationLink = this.$cache.my.invitationLink
                    this.myInviter = this.$cache.my.myInviter
                }
            } else {
                this.$cache.my = {}
            }

            this.getData()
        },
        getData() {
            this.$contract.levelSub.methods.LevelOf(this.$address).call((err, res) => {
                if (!err) {
                    this.grade = Number(res)
                    this.$cache.my.grade = this.grade

                    this.$contract.levelSub.methods.CanUpgradeLv(this.$address).call((err, res) => {
                        if (!err && Number(res) > 0 && this.grade < 4) {
                            this.show = true
                            this.updateGrade = res
                        }
                    })
                }
            })

            this.$contract.recommend.methods.GetIntroducer(this.$address).call((err, res) => {
                if (!err && Number(res) > 0) {
                    this.$contract.recommend.methods.AddressToShortCode(res).call((error, result) => {
                        if (!error) {
                            if (Number(result) > 0) {
                                this.myInviter = this.$web3.utils.hexToString(result)
                            } else {
                                this.myInviter = res
                            }

                            this.$cache.my.myInviter = this.myInviter
                        }
                    })
                }
            })

            this.$contract.recommend.methods.AddressToShortCode(this.$address).call((err, res) => {
                if (!err) {
                    let invitationCode = this.$web3.utils.hexToString(res)
                    if (invitationCode === "") { return }
                    
                    this.invitationCode = invitationCode
                    this.invitationLink = `${location.href}?invitationCode=${invitationCode}`

                    this.$cache.my.invitationCode = invitationCode
                    this.$cache.my.invitationLink = this.invitationLink
                }
            })

            this.$contract.levelSub.methods.Noders().call((err, res) => {
                if (!err) {
                    let flag = false
                    res.forEach(item => {
                        if (item.toUpperCase() === this.$address.toUpperCase()) {
                            flag = true
                        }
                    })

                    this.path = flag ? "/my/view/drawDividend" : "/my/view/JoinNode"
                    this.$cache.my.path = this.path
                }
            })
        }
    },
    mounted() {
        let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale
        if (guide) {
            this.init()
        } else {
            this.isBind = false
        }
    }
};
</script>

<style lang="scss" scoped>
.grade-btn {
    border: none;
    outline: none;
    width: 340px;
    height: 72px;
    border-radius: 8px;
    color: #fff;
    font-size: 32px;
    margin-top: 8px;
    background-color: #0083f1;
}
.grade-bg {
    width: 564px;
    height: 692px;
    margin: 0 auto;
    padding-top: 76px;
    position: relative;
    & > img {
        width: 100%;
        height: 100%;
        margin-left: -40px;
    }
    & > span {
        position: absolute;
        left: 40%;
        bottom: 115px;
        color: #fff;
        font-size: 64px;
    }
}
.box {
    padding: 0 40px;
    padding-top: 40px;
    width: 100vw;
    overflow-x: hidden;

    &>img {
        width: 100%;
        padding-top: 20px;
        margin-left: -50px;
        margin-bottom: -30px;
    }

    .myCode {
        width: 710px;
        padding: 7px 40px;
        background: linear-gradient(
            134deg,
            rgba(14, 149, 246, 1) 0%,
            rgba(24, 90, 235, 1) 100%
        );
        border-radius: 16px;
        margin: 0 auto 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
        transform: translateX(-20px);

        &>a {
            transform: translateY(10px) translateX(-5px);
        }

        img {
            width: 130px;
            height: 86px;
        }

        & > div {
            display: flex;
            flex-direction: column;
            margin: 0 130px 0 72px;
            & > span {
                font-size: 32px;
            }
            & > p {
                font-size: 52px;
            }
        }

        .van-iconfont {
            margin-top: 22px;
        }
    }

    .myContent {
        .myContentList {
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 32px;
            color: #fff;
            padding-bottom: 28px;
            border-bottom: 1px solid rgba(204, 204, 204, 1);
            margin-bottom: 28px;

            & > div:nth-child(1) {
                display: flex;
                align-items: center;

                .van-iconfont {
                    color: #4a92fc;
                    font-size: 28px;
                    margin-right: 10px;
                }
            }

            & > span {
            }

            & > a {
                color: #4d95fc;
            }

            i {
                font-size: 40px !important;
            }

            & > div:last-child {
                color: #4d95fc;
                position: relative;

                .van-iconfont {
                    font-size: 22px;
                }

                & > div {
                    position: absolute;
                    width: 60px;
                    height: 3px;
                    background-color: #4d95fc;
                    transform: rotateZ(-40deg);
                    top: 20px;
                }
            }
        }

        .myContentList:last-child {
            margin-bottom: 0;
        }
    }
}
</style>