import Vue from 'vue'
import axios from 'axios'
import VueI18n from 'vue-i18n'
import messages from './static/language'
import App from './App.vue'
import './registerServiceWorker'
import 'amfe-flexible/index.min.js'
import router from './router'
import { Lazyload } from 'vant'
import VueClipboard from 'vue-clipboard2'
import selfIcon from '@/components/self-icon.vue'
import { $web3, contract, fromWei, toWei } from './static/initData'

Vue.config.productionTip = false
Vue.component('self-icon', selfIcon)
Vue.use(Lazyload)
Vue.use(VueClipboard)
Vue.use(VueI18n)

Vue.prototype.$http = axios.create({
  headers: { 'Content-Type': 'application/json' },
  baseURL: 'http://106.13.199.145:8081/eth-jsonrpc/8c5eebe5-fcfa-1b56-3e09-cc1a56093052'
})
Vue.prototype.$web3 = $web3
Vue.prototype.$contract = contract
Vue.prototype.$fromWei = fromWei
Vue.prototype.$toWei = toWei
Vue.prototype.$cache = {}

const i18n = new VueI18n({
  locale: 'zh-CN',
  messages
})

initAddress(() => {
  new Vue({
    i18n,
    router,
    render: h => h(App)
  }).$mount('#app')
})

function initAddress(callback) {
  if (window.imToken) {
    imToken.callAPI("user.getCurrentAccount", (err, address) => {
      if (!err) {
        Vue.prototype.$address = address
        callback()
      }
    })
  } else if (window.ethereum) {
    if (window.ethereum.isTrust || window.ethereum.isHuobi) {
      Vue.prototype.$address = ethereum.address
      callback()
    } else {
      ethereum.enable().then(res => {
        Vue.prototype.$address = ethereum.selectedAddress
        callback()
        ethereum.on("accountsChanged", accounts => {
          let address = Vue.prototype.$address
          if (accounts[0] != address) {
            Vue.prototype.$address = accounts[0]
            
            if (router.history.current.name !== 'home') {
              router.push({ path: '/' })
              location.reload()
            } else {
              router.go(0)
            }
  
          }
        })
      })
    }
  } else {
    Vue.prototype.$address = ''
	  callback()
  }
}