import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/home/Home.vue'
import Income from '../views/home/view/Income.vue'
import Join from '../views/home/view/Join.vue'
import AgainJoin from '../views/home/view/AgainJoin.vue'
import Compensate from '../views/home/view/Compensate.vue'
import Record from '../views/home/view/Record.vue'
import Change from '../views/change/Change.vue'
import ChangeRecord from '../views/change/ChangeRecord.vue'
import My from '../views/my/My.vue'
import MyLnvitationList from '../views/my/view/MyLnvitationList.vue'
import JoinNode from '../views/my/view/JoinNode.vue'
import DrawDividend from '../views/my/view/DrawDividend.vue'
import NodeRewardList from '../views/my/view/NodeRewardList.vue'
import BuyRating from '../views/my/view/BuyRating.vue'
import CompensateRecord from '../views/my/view/CompensateRecord.vue'
import ManagerAssets from '../views/my/view/ManagerAssets.vue'
import Exit from '../views/my/view/Exit.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    meta: {
      tabbar: true
    }
  },
  {
    path: '/change',
    name: 'change',
    component: Change,
    meta: {
      tabbar: true
    }
  },
  {
    path: '/changeRecord',
    name: 'changeRecord',
    component: ChangeRecord
  },
  {
    path: '/my',
    name: 'my',
    component: My,
    meta: {
      tabbar: true
    }
  },
  {
    path: '/my/view/myLnvitationList',
    name: 'myLnvitationList',
    component: MyLnvitationList
  },
  {
    path: '/income',
    name: 'income',
    component: Income
  },
  {
    path: '/my/view/joinNode',
    name: 'joinNode',
    component: JoinNode
  },
  {
    path: '/my/view/drawDividend',
    name: 'drawDividend',
    component: DrawDividend
  },
  {
    path: '/join',
    name: 'join',
    component: Join
  },
  {
    path: '/againJoin',
    name: 'againJoin',
    component: AgainJoin
  },
  {
    path: '/my/view/nodeRewardList',
    name: 'nodeRewardList',
    component: NodeRewardList
  },
  {
    path: '/compensate',
    name: 'compensate',
    component: Compensate
  },
  {
    path: '/record',
    name: 'record',
    component: Record
  },
  {
    path: '/my/view/buyRating',
    name: 'buyRating',
    component: BuyRating
  },
  {
    path: '/my/view/compensateRecord',
    name: 'compensateRecord',
    component: CompensateRecord
  },
  {
    path: '/my/view/managerAssets',
    name: 'managerAssets',
    component: ManagerAssets
  },
  {
    path: '/my/view/exit',
    name: 'exit',
    component: Exit
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
