<template>
    <div class="box">
        <img v-lazy="require('../../../assets/exit.png')">
        <div class="text">
            <span>{{ $t('message.exitTitle1') }}</span>
            <span>{{ $t('message.exitTitle2') }}</span>
        </div>
        <div class="amount">
            <div>
                <div>
                    <self-icon class="icon" type="outline"></self-icon>
                    <span>{{ $t('message.totalJoin') }}</span>
                </div>
                <span>{{ totalJoin }} ETH</span>
            </div>
            <div>
                <div>
                    <self-icon class="icon" type="dividend-income"></self-icon>
                    <span>{{ $t('message.totalIncome') }}</span>
                </div>
                <span>{{ totalIncome }} ETH</span>
            </div>
        </div>
        <div class="exit">
            <div>
                <self-icon type="exit"></self-icon>
                <span>{{ $t('message.canExit') }}</span>
            </div>
            <div>{{ exitAmount }} ETH</div>
        </div>
        <Button @click="exit" :disabled="disabled">{{ $t('message.define') }}</Button>
    </div>
</template>

<script>
import { Button } from 'vant'

export default {
    components: { Button },
    data() {
        return {
            disabled: true,
            totalJoin: '0.00',
            exitAmount: '0.00',
            totalIncome: '0.00'
        }
    },
    methods: {
        exit() {
            this.$contract.round.methods.Redeem().send({
                from: this.$address
            }).on('error' , console.error)
            .on('receipt' , result=>{
                this.$router.back()
            })
        },
        init() {
            if (this.$cache.exit) {
                this.disabled = this.$cache.exit.disabled
                this.totalJoin = this.$cache.exit.totalJoin
                this.exitAmount = this.$cache.exit.exitAmount
                this.totalIncome = this.$cache.exit.totalIncome
            } else {
                this.$cache.exit = {}
            }

            this.getData()
        },
        getData() {
            this.$contract.round.methods.GetCurrentGameStatus()
            .call({ from: this.$address }, (err, res) => {
                if (!err) {
                    let lastTimestamp = new Date(Number(res.latestDepositInTime) * 1000 + 7200000).getTime()

                    this.$contract.round.methods.TotalInOutAmount()
                    .call({ from: this.$address }, (error, result) => {
                        if (!error) {
                            let inEther = this.$fromWei(result.inEther),
                                outEther = this.$fromWei(result.outEther)

                            this.totalJoin = `${inEther.toFixed(2)}`
                            this.totalIncome = `${outEther.toFixed(2)}`
                            this.exitAmount = `${(inEther - outEther).toFixed(2)}`
                            this.disabled = !(inEther > outEther && new Date().getTime() > lastTimestamp)

                            this.$cache.exit.disabled = this.disabled
                            this.$cache.exit.totalJoin = this.totalJoin
                            this.$cache.exit.exitAmount = this.exitAmount
                            this.$cache.exit.totalIncome = this.totalIncome
                        }
                    })
                }
            })
        }
    },
    mounted() {
        let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale
        this.init()
    }
}
</script>

<style lang="scss" scoped>
.box {
    padding: 90px 23px 0;
    &>button {
        border: none;
        outline: none;
        width: 340px;
        height: 72px;
        color: #fff;
        font-size: 32px;
        margin-top: 52px;
        border-radius: 8px;
        background-color: #3782FB;
    }
    &>.exit {
        width: 100%;
        color: #CECDCD;
        padding: 23px 0;
        padding-left: 24px;
        border-radius: 20px;
        background-color: #4D27B3;
        display: flex;
        align-items: center;
        &>div:first-child {
            display: flex;
            align-items: center;
            flex-direction: column;
            &>span {
                font-size: 32px;
                white-space: nowrap;
            }
            &>i {
                color: #3782FB;
                font-size: 80px;
                margin-bottom: 16px;
            }
        }
        &>div:last-child {
            width: 100%;
            font-size: 60px;
        }
    }
    &>.amount {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 18px 0;
        &>div {
            width: 344px;
            color: #CECDCD;
            padding: 30px 42px 20px 68px;
            background-color: #4D27B3;
            border-radius: 20px;
            &>div {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                &>.icon {
                    color: #3782FB;
                    font-size: 60px;
                    margin-right: 24px;
                }
                &>span {
                    font-size: 32px;
                }
            }
            &>span {
                font-size: 40px;
            }
        }
    }
    &>.text {
        width: 100%;
        padding: 38px 29px 26px;
        border: 2px solid #3782FB;
        border-radius: 20px;
        color: #CECDCD;
        font-size: 32px;
        display: flex;
        flex-direction: column;
        text-align: left;
        margin-top: 36px;
    }
    &>img {
        width: 171px;
        height: 192px;
    }
}
</style>