<template>
    <div class="box">
        <div class="header">
            <div>
                <div>
                    <img v-lazy="require('../../../assets/income-header.png')">
                    <span>{{ canWithdraw }}</span>
                </div>
                <span>{{ $t('message.canDraw') }}/ETH</span>
            </div>
            <router-link to="/record?type=">
                <self-icon type="income-record"></self-icon>
                <span>{{ $t('message.incomeRecord') }}</span>
            </router-link>
        </div>
        <div class="content">
            <Grid :border="false" :gutter="1" :column-num="2">
                <grid-item class="content-item">
                    <self-icon class="icon" type="dividend-income"></self-icon>
                    <span>{{ $t('message.dividendIncome') }}</span>
                    <span>{{ dividendIncome }} ETH</span>
                </grid-item>
                <grid-item class="content-item">
                    <self-icon class="icon" type="promote-income"></self-icon>
                    <span>{{ $t('message.promoteIncome') }}</span>
                    <span>{{ promoteIncome }} ETH</span>
                </grid-item>
                <grid-item class="content-item">
                    <self-icon class="icon" type="outline"></self-icon>
                    <span>{{ $t('message.outQuota') }}</span>
                    <span>{{ outAmount }} ETH</span>
                </grid-item>
                <grid-item class="content-item">
                    <self-icon class="icon" type="total-record"></self-icon>
                    <span>{{ $t('message.totalIncome') }}</span>
                    <span>{{ totalIncome }} ETH</span>
                </grid-item>
            </Grid>
        </div>
        <div class="btn">
            <Button type="info" size="large" @click="getIncome" :disabled="canWithdraw <= 0">{{ $t('message.draw') }}</Button>
            <span>{{ $t('message.gasPrice') }}{{ gasPrice }} SFC</span>
        </div>
    </div>
</template>

<script>
import { Grid, GridItem, Button, Toast } from 'vant'

export default {
    components: { Grid, GridItem, Button },
    data() {
        return {
            canWithdraw: '0.00',
            dividendIncome: '0.00',
            promoteIncome: '0.00',
            outAmount: '0.00',
            totalIncome: '0.00',
            sfcBalance: 0,
            gasPrice: 0
        }
    },
    methods: {
        getIncome() {
            if (this.sfcBalance < this.gasPrice) {
                return Toast.fail(this.$t('message.notSFC'))
            }

            this.$contract.round.methods.WithdrawProfix().send({
                from: this.$address
            }).on('transactionHash', txHash => {
                console.log(txHash)
            }).on('error', console.error)
            .on('receipt', res => {
                this.getData()
            })
        },
        init() {
            if (this.$cache.income) {
                this.canWithdraw = this.$cache.income.canWithdraw
                this.dividendIncome = this.$cache.income.dividendIncome
                this.promoteIncome = this.$cache.income.promoteIncome
                this.outAmount = this.$cache.income.outAmount
                this.totalIncome = this.$cache.income.totalIncome
                this.sfcBalance = this.$cache.income.sfcBalance
                this.gasPrice = this.$cache.income.gasPrice
            } else {
                this.$cache.income = {}
            }
            
            this.getData()
        },
        getData() {
            this.$contract.round.methods.GetCurrentGameStatus()
            .call({ from: this.$address }, (err, res) => {
                if (!err) {
                    let outAmount = this.$fromWei(res.currentEther) * Number(res.profixMultiplier) - this.$fromWei(res.withdrawableTotal)

                    this.$contract.round.methods.CurrentCanWithdrawProfix(this.$address).call((err, res) => {
                        if (!err) {
                            let st = this.$fromWei(res.st),
                                dy = this.$fromWei(res.dy),
                                canWithdraw = st + dy
                            
                            this.$contract.cost.methods.WithdrawCost(this.$toWei(`${canWithdraw}`)).call((err, res) => {
                                if (!err) {
                                    this.gasPrice = this.$fromWei(res)
                                    this.$cache.income.gasPrice = this.gasPrice
                                }
                            })

                            if (canWithdraw > outAmount) {
                                this.canWithdraw = outAmount.toFixed(2)
                            } else {
                                this.canWithdraw = canWithdraw.toFixed(2)
                            }
                            this.dividendIncome = st.toFixed(2)
                            this.promoteIncome = dy.toFixed(2)

                            this.$cache.income.canWithdraw = this.canWithdraw
                            this.$cache.income.dividendIncome = this.dividendIncome
                            this.$cache.income.promoteIncome = this.promoteIncome
                        }
                    })
                    
                    this.outAmount = outAmount.toFixed(2)
                    this.$cache.income.outAmount = this.outAmount
                }
            })

            this.$contract.round.methods.TotalInOutAmount()
            .call({ from: this.$address }, (err, res) => {
                if (!err) {
                    this.totalIncome = this.$fromWei(res.outEther, 2)
                    this.$cache.income.totalIncome = this.totalIncome
                }
            })

            this.$contract.erc20token.methods.balanceOf(this.$address).call((err, res) => {
                if (!err) {
                    this.sfcBalance = this.$fromWei(res)
                    this.$cache.income.sfcBalance = this.sfcBalance
                }
            })
        }
    },
    mounted() {
        let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale
        this.init()
    }
}
</script>

<style lang="scss">
.van-grid-item__content {
    background-color: #4D27B3;
    color: #CECDCD;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
</style>

<style lang="scss" scoped>
.btn {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 60px;
    &>button {
        width: 340px;
        margin-bottom: 20px;
        border-radius: 8px;
    }
    &>span {
        color: #fff;
        font-size: 24px;
    }
}
.content {
    width: 670px;
    height: 500px;
    margin: 0 auto;
    border-radius: 20px;
    overflow: hidden;
    &>.van-grid {
        width: 100%;
        height: 100%;
    }
    .content-item {
        .icon {
            font-size: 60px;
            color: #0083F1;
        }
        span:nth-of-type(1) {
            font-size: 32px;
            font-weight: 400;
        }
        span:nth-of-type(2) {
            font-size: 40px;
            font-weight: 400;
        }
    }
}
.header {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    padding: 40px 0;
    padding-right: 20px;

    &>div {
        display: flex;
        align-items: center;
        flex-direction: column;
        color: #fff;
        margin-right: 46px;
        &>div {
            position: relative;
            width: 236px;
            height: 240px;
            &>img {
                width: 100%;
                height: 100%;
            }
            &>span {
                display: inline-block;
                width: 200px;
                height: 200px;
                line-height: 200px;
                position: absolute;
                top: 30px;
                left: 25px;
                font-size: 48px;
            }
        }
        &>span {
            font-size: 28px;
            margin-top: 40px;
        }
    }
    &>a {
        display: flex;
        align-items: center;
        color: #0083F1;
        &>.icon-income-record {
            font-size: 55px;
            margin-right: 20px;
        }
        &>span {
            font-size: 32px;
            text-decoration: underline;
        }
    }
}
</style>