                    display: flex;
                    justify-content: space-between;
                    z-index: 3;
                }
            }
            & > div:nth-child(1) {
                border: 2px solid #dedcdc;
                background-color: transparent;

                & > div:last-child {
                    position: absolute;
                    left: -2px;
                    top: -5%;
                    width: 56.5%; //101
                    height: 110%;
                    background: linear-gradient(
                        270deg,
                        rgba(125, 179, 255, 1) 0%,
                        rgba(0, 131, 241, 1) 13%,
                        rgba(0, 131, 241, 1) 100%
                    );
                    box-shadow: 4px 0px 8px rgba(255, 255, 255, 0.47);
                    border-radius: 12px 8px 8px 12px;
                    z-index: 2;
                }
            }
            & > div:nth-child(2) {
                width: 80%;
            }
            & > div:nth-child(3) {
                width: 70%;
            }
            & > div:nth-child(4) {
                width: 60%;
            }
        }

        .changInput {
            .changInputBox {
                display: flex;
                justify-content: space-between;
                align-items: center;

                & > div {
                    width: 272px;
                    height: 70px;
                    border: 2px solid #dedcdc;
                    border-radius: 12px;
                    display: flex;
                    font-size: 36px;
                    padding: 0 20px;
                    align-items: center;

                    & > input {
                        width: 100%;
