                }
            }

            .changeButton {
                margin-top: 58px;
                display: flex;
                flex-direction: column;
                align-items: center;

                & > button {
                    width: 340px;
                    height: 72px;
                    border-radius: 8px;
                    background-color: #0083f1;
                    border: none;
                    font-size: 32px;
                    margin-bottom: 22px;
                    color: #fff;
                }

                & > div {
                    font-size: 25px;
                    color: #ccc;
                    display: flex;
                    align-items: center;

                    & > img {
