<template>
	<div class="box">
		<div class="title">{{ $t('message.dividendRecord') }}</div>
		<div class="switchState">
			<div>{{ $t('message.dividendAmount') }}</div>
			<div>{{ $t('message.time') }}</div>
		</div>
		<div class="contentList">
			<div class="list" v-for="(item, index) of list" :key="index">
				<div>{{ item.amount }} ETH</div>
				<div>{{ item.time }}</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			list: []
		}
	},
	methods: {
		init() {
			this.getData()
		},
		getData() {
			this.$contract.luckAssetsPoolA.events.Log_WinningThePrized({
				filter: { owner: this.$address },
				fromBlock: 0
			}).on('data', res => {
				console.log(res)
			}).on('error', console.log)
		}
	},
	mounted() {
		let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale
		this.init()
	}
}
</script>

<style lang="scss" scoped>
	.box{
		width: 100vw;
		height: 100vh;
		display: flex;
		flex-direction: column;
		color: #fff;
		
		.title{
			padding: 36px 0;
			font-size: 32px;
		}
		
		.switchState,.contentList{
			font-size: 24px;
		}
		
		.switchState{
			width: 100%;
			display: flex;
			padding: 18px 0;
			border-top: 1px solid #c4c4c4;
			border-bottom: 1px solid #c4c4c4;
			
			div{
				flex: 1;
				text-align: center;
			}
		}
		
		.contentList{
			flex: 1;
			width: 100%;
			overflow-y: scroll;
			
			.list{
				display: flex;
				margin-top: 20px;
				
				div{
					flex: 1;
					text-align: center;
				}
			}
		}
	}
</style>
