<template>
	<div class="box">
		<div class="title">{{ $t('message.inviteList') }}</div>
		<div class="switchState">
			<div>{{ $t('message.myBuddy') }}</div>
			<div>{{ $t('message.grade') }}</div>
			<div>{{ $t('message.totalJoin') }}/ETH</div>
			<div>{{ $t('message.communityPlayer') }}</div>
		</div>
		<div class="contentList">
			<div class="list" v-for="(item, index) of list" :key="index">
				<div>{{ item.partner }}</div>
				<div>V{{ item.state }}</div>
				<div>{{ item.totalJoin }}</div>
				<div>{{ item.member }}</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			list: []
		}
	},
	methods: {
		init() {
			if (this.$cache.myLnvitationList) {
				this.list = this.$cache.myLnvitationList.list
			} else {
				this.$cache.myLnvitationList = {}
			}

			this.getData()
		},
		async getData() {
			let list = [],
				arr = await this.$contract.recommend.methods.RecommendList(this.$address, 0).call()

			for (let key in arr) {
				if (arr.hasOwnProperty(key)) {
					let partner = await this.$contract.recommend.methods.AddressToShortCode(arr[key]).call(),
						state = await this.$contract.levelSub.methods.LevelOf(arr[key]).call(),
						totalJoin = this.$fromWei((await this.$contract.round.methods.GetCurrentGameStatus().call({ from: arr[key] })).currentEther),
						member = await this.$contract.recommend.methods.TeamMemberTotal(arr[key]).call()

					if (Number(partner) === 0) {
						partner = this.$t('message.notCode')
					} else {
						partner = this.$web3.utils.hexToString(partner)
					}

					let row = {
						partner: partner,
						state: state,
						totalJoin: totalJoin,
						member: member
					}

					list.push({ ...row })
				}
			}

			this.list = list
			this.$cache.myLnvitationList.list = list
		}
	},
	mounted() {
		let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale
		this.init()
	}
}
</script>

<style lang="scss" scoped>
	.box{
		width: 100vw;
		height: 100vh;
		display: flex;
		flex-direction: column;
		color: #fff;
		
		.title{
			padding: 36px 0;
			font-size: 32px;
		}
		
		.switchState,.contentList{
			font-size: 24px;
		}
		
		.switchState{
			width: 100%;
			display: flex;
			padding: 18px 0;
			border-top: 1px solid #c4c4c4;
			border-bottom: 1px solid #c4c4c4;
			
			div{
				flex: 1;
				text-align: center;
			}
		}
		
		.contentList{
			flex: 1;
			width: 100%;
			overflow-y: scroll;
			
			.list{
				display: flex;
				margin-top: 20px;
				
				div{
					flex: 1;
					text-align: center;
				}
			}
		}
	}
</style>
