<template>
    <div>
        <div class="header">
            <span>{{ $t('message.drawRecord') }}</span>
        </div>
        <record-list :list="list"></record-list>
    </div>
</template>

<script>
import RecordList from '@/components/record-list'

export default {
    components: { RecordList },
    data() {
        return {
            list: []
        }
    },
    mounted() {
        let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale

        this.$contract.redressWS.events.Event_AddNewRedress({
filter:{ owner: this.$address },
fromBlock:9130000
}).on('data', res => {
			this.list.push({
				type: 'SFC',
				amount: `${this.$fromWei(res.returnValues.amount)} SFC`,
				time: '2020.01.15'
			})
		}).on('error', console.error)
		
		this.$contract.redressWS.events.Event_WithdrawRedress({
			filter:{ owner: this.$address },
fromBlock:9130000
}).on('data', res => {
			this.list.push({
				type: 'SFC',
				amount: `${this.$fromWei(res.returnValues.amount)} SFC`,
				time: '2020.01.15'
			})
		}).on('error', console.error)
    }
}
</script>

<style lang="scss" scoped>
.header {
    width: 100%;
    height: 118px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 2px solid #C4C4C4;
    &>span {
        color: #fff;
        font-size: 32px;
        font-weight: bold;
        padding: 12px 0;
    }
}
</style>