<template>
    <div class="box">
		<div class="header" v-if="$i18n.locale === 'zh-CN'">
			<p></p>
			<p>
				<span>1.</span>
				<span>13200ETH</span>
			</p>
			<p>
				<span>2.</span>
				<span>V2</span>
			</p>
			<p>
				<span>3.</span>
				<span>7</span>
			</p>
		</div>
		<div class="header" v-else>
			<p>Alliance partner:</p>
			<p>
				<span>.</span>
				<span>50 eth for top 20 participants and 100 eth for later 20 participants;</span>
			</p>
			<p>
				<span>.</span>
				<span>All players participating in the alliance partners will be promoted to V2;</span>
			</p>
			<p>
				<span>.</span>
				<span>The alliance partners will receive bonus once a week. Dividend time is determined by the rate of block issuance in 7 days.</span>
			</p>
		</div>
		<span v-if="$i18n.locale === 'zh-CN'">{{ player }}</span>
		<span v-else>Currently, there are {{ player }} players participating in the alliance partners</span>
		<span v-if="$i18n.locale === 'zh-CN'">{{ prices }}ETH</span>
		<span v-else>You need to pay {{ prices }} eth to participate</span>
		<div class="content">
			<div class="title">
				<i></i>
				<span>{{ $t('message.joinNode') }}</span>
			</div>
			<div class="input">
				<input type="number" v-model.number="joinAmount">
				<span>ETH</span>
			</div>
			<div class="amount">
				<img v-lazy="require('../../../assets/eth.png')">
				<span>{{ balance }} ETH</span>
			</div>
		</div>
		<div class="btn">
			<Button type="info" size="large" @click="joinNode">{{ $t('message.join') }}</Button>
		</div>
    </div>
</template>

<script>
import { Button, Toast } from 'vant'

export default {
	components: { Button },
	data() {
		return {
			balance: '0.00',
			player: '0',
			prices: '0',
			joinAmount: 0
		}
	},
	methods:{
		joinNode() {
			if (this.joinAmount < this.prices) {
				return Toast.fail(`${this.$t('message.minJoin')}${this.prices}ETH`)
			}

			this.$contract.levelSub.methods.PaymentToUpgradeNoderL2().send({
				from: this.$address,
				value: this.$toWei(`${this.joinAmount}`)
			}).on('transactionHash', txHash => {
				console.log(txHash)
			}).on('error', console.error)
			.on('receipt', res => {
				console.log(res)
				this.joinAmount = 0
				this.getData()
			})
		},
		init() {
			if (this.$cache.joinNode) {
				this.balance = this.$cache.joinNode.balance
				this.player = this.$cache.joinNode.player
				this.prices = this.$cache.joinNode.prices
				this.joinAmount = this.prices
			} else {
				this.$cache.joinNode = {}
			}
			
			this.getData()
		},
		getData() {
			this.$web3.eth.getBalance(this.$address).then(balance => {
				this.balance = this.$fromWei(balance, 2)
				this.$cache.joinNode.balance = this.balance
			})

			this.$contract.levelSub.methods._paymentedCount().call((err, res) => {
				if (!err) {
					this.player = res
					this.$cache.joinNode.player = res
				}
			})

			this.$contract.levelSub.methods._paymentLv2Prices().call((err, res) => {
				if (!err) {
					this.prices = this.$fromWei(res)
					this.joinAmount = this.prices
					this.$cache.joinNode.prices = this.prices
				}
			})
		}
	},
	mounted() {
		let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale
		this.init()
	}
}
</script>

<style lang="scss" scoped>
.btn {
	width:340px;
	margin: 118px auto 0;
}
.content {
	padding: 0 40px;
	margin-top: 60px;
	.amount {
		display: flex;
		align-items: center;
		font-size: 24px;
		font-weight: 400;
		color: #FCFCFC;
		margin-top: 20px;
		&>img {
			width: 40px;
			height: 40px;
			margin-right: 12px;
		}
	}
	.input {
		width: 100%;
		padding: 20px 0 16px;
		color: #fff;
		font-size: 48px;
		font-weight: bold;
		display: flex;
		align-items: center;
		border-bottom: 2px solid #DEDCDC;
		&>input {
			font-size: 48px;
			font-weight: bold;
			width: 100%;
			border: none;
			outline: none;
			background-color: transparent;
		}
	}
	&>.title {
		display: flex;
		align-items: center;
		&>i {
			width: 8px;
			height: 32px;
			background-color: #4D95FC;
			margin-right: 12px;
		}
		&>span {
			font-size: 32px;
			color: #fff;
			font-weight: bold;
		}
	}
}
.box>span:nth-of-type(1) {
	display: inline-block;
	font-size: 36px;
	color: #fff;
	font-weight: bold;
	margin-top: 40px;
}
.box>span:nth-of-type(2) {
	display: inline-block;
	font-size: 28px;
	color: #fff;
	font-weight: bold;
		margin-top: 40px;
}
.header {
	width: 670px;
	padding: 20px 38px 20px 20px;
	background-color: #4D27B3;
	color: #fff;
	margin: 0 auto;
	border-radius: 20px;
	text-align: left;
	&>p {
		font-size: 24px;
		margin-bottom: 8px;
		display: flex;
		align-items: flex-start;
		line-height: 1.2;
	}
	&>p:first-child {
		font-size: 28px;
	}
	&>p:last-child {
		margin: 0;
	}
}
.box {
    padding: 40px 20px 0;
}
</style>