<template>
    <div class="header">
        <div id="chart"></div>
        <span>/SFC</span>
    </div>
</template>

<script>
import echarts from 'echarts'
import '@/components/echarts-liquidfill.min'

export default {
    props: { amount: String },
    data() {
        return {
            WaterPolo: ''
        }
    },
    mounted() {
        this.WaterPolo = echarts.init(document.getElementById('chart'))
        this.WaterPolo.setOption({
            series: [{
                type: 'liquidFill',
                radius: '110%',
                data: [{
                    value: 0.5,
                    direction: 'left',
                    itemStyle: {
                        normal: {
                            color: '#40AFFF'
                        }
                    }
                }, {
                    value: 0.5,
                    direction: 'right',
                    itemStyle: {
                        normal: {
                            color: '#40AFFF'
                        }
                    }
                }],
                backgroundStyle: {
                    borderWidth: 3,
                    borderColor: '#40AFFF',
                    color: '#fff'
                },
                label: {
                    normal: {
                        formatter: this.amount,
                        textStyle: {
                            fontSize: 20
                        }
                    }
                }
            }]
        })
    },
    watch: {
        amount(val) {
            this.WaterPolo.setOption({
                series: [{
                    label: {
                        normal: {
                            formatter: val
                        }
                    }
                }]
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.header {
    width: 240px;
    margin: 0 auto;
    padding: 60px 0 40px;
    &>#chart {
        width: 240px;
        height: 240px;
        border-radius: 50%;
        overflow: hidden;
    }
    &>span {
        font-size: 28px;
        color: #fff;
    }
}
</style>