<template>
    <div class="box">
		<div class="NodeReward">
			<router-link class="NodeRewardBox" to="/my/view/NodeRewardList">
				<self-icon type="income-record"></self-icon>
				<span>{{ $t('message.dividendRecord') }}</span>
			</router-link>
			<div class="NodeRewardContent">
				<div class="NodeRewardDate">
					<span>{{ time }}</span>
					<span>{{ date }}</span>
				</div>
			</div>
			<span>{{ stateText }}</span>
			<p v-if="disabled">{{ $t('message.await') }}</p>
			<p v-else>{{ $t('message.get') }}{{ obtainETH }}ETH</p>
			<Button :disabled="disabled" @click="withdraw">{{ $t('message.draw') }}</Button>
		</div>
    </div>
</template>

<script>
import { Button } from 'vant'

export default {
	components: { Button },
	data() {
		return {
			time: '2019-12-31',
			date: '12:00',
			disabled: true,
			stateText: this.$t('message.dividendTime'),
			obtainETH: '0'
		}
	},
	methods:{
		withdraw() {
			this.$contract.noderAssets.methods.WithdrawProfix().send({
				from: this.$address
			}).on('transactionHash', txHash => {
				console.log(txHash)
			}).on('error', console.error)
			.on('receipt', res => {
				this.disabled = true
				this.getData()
			})
		},
		init() {
			if (this.$cache.drawDividend) {
				this.time = this.$cache.drawDividend.time
				this.date = this.$cache.drawDividend.date
				this.disabled = this.$cache.drawDividend.disabled
				this.stateText = this.$cache.drawDividend.stateText
				this.obtainETH = this.$cache.drawDividend.obtainETH
			} else {
				this.$cache.drawDividend = {}
			}
			
			this.getData()
		},
		getData() {
			this.$contract.noderAssets.methods._latestBonusTime().call((err, res) => {
				if (!err) {
					// let date = new Date(Number(res) * 1000 + 3.5 * 3600000),
					let date = new Date(Number(res) * 1000 + (168 * 3600000)),
						year = date.getFullYear(),
						month = date.getMonth() + 1 < 10 ? `0${date.getMonth() + 1}` : date.getMonth() + 1,
						day = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate(),
						hours = date.getHours() < 10 ? `0${date.getHours()}` : date.getHours(),
						minutes = date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes()

					this.time = `${year}-${month}-${day}`
					this.date = `${hours}:${minutes}`
					this.$cache.drawDividend.time = this.time
					this.$cache.drawDividend.date = this.date
					
					if (new Date().getTime() >= date.getTime()) {
						this.stateText = this.$t('message.dividendReward')
					} else {
						this.stateText = this.$t('message.dividendTime')
					}

					this.$cache.drawDividend.stateText = this.stateText
				}
			})

			this.$contract.noderAssets.methods.MyProfix()
			.call({ from: this.$address }, (err, res) => {
				if (!err) {
					this.obtainETH = this.$fromWei(res, 2)
					this.$cache.drawDividend.obtainETH = this.obtainETH

					if (this.obtainETH > 0) {
						this.disabled = false
					} else {
						this.disabled = true
					}

					this.$cache.drawDividend.disabled = this.disabled
				}
			})
		}
	},
	mounted() {
		let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale
		this.init()
	}
}
</script>

<style lang="scss" scoped>
.NodeReward{
	color: #fff;
	
	&>.NodeRewardBox{
		display: flex;
		color: #0083F1;
		font-size: 32px;
		justify-content: flex-end;
		align-items: center;
		
		.van-iconfont{
			font-size: 24px;
		}
		
		&>span{
			text-decoration: underline;
			margin-left: 10px;
		}
	}
	
	&>.NodeRewardContent{
		display: flex;
		flex-direction: column;
		align-items: center;
		margin: 88px 0 40px;
		
		&>.NodeRewardDate{
			width:240px;
			height:240px;
			background:linear-gradient(180deg,rgba(64,175,255,1) 0%,rgba(53,115,250,1) 100%);
			border-radius:50%;
			font-size:38px;
			font-family:DIN;
			font-weight:bold;
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
		}
		
	}

	&>span{
		font-size: 28px;
	}
	
	&>p{
		font-size: 40px;
		margin: 40px 0 160px;
	}
	
	&>button{
		width:340px;
		height:72px;
		border-radius:8px;
		border: none;
		font-size: 32px;
		color: #fff;
		background: #0083F1;
	}

}
.box {
    padding: 40px 20px 0;
}
</style>