<template>
    <div class="van-tabbar-page box">
        <NoticeBar
          style="position: fixed; top: 0; left: 0; width: 100vw; z-index: 99999"
          text="SmurfSFCimTokenimTokenLogoSFC"
          color="#fff"
          background="rgba(34,9,100,.85)"
          left-icon="volume-o"
          v-show="isNotic"
          @click="noticeBarEvent"
        ></NoticeBar>
        <div class="notice" v-show="!isNotic">
            <Icon class="notice-close" name="cross" @click="noticeBarEvent" />
            <Steps
              direction="vertical"
              style="background-color: transparent; text-align: left;"
              active-color="#D1D1D1"
              active-icon="stop"
              inactive-icon="stop"
            >
                <Step>
                    <p class="step1">2020/03/17 16:00</p>
                    <h3 class="step2">SFCimToken</h3>
                    <p class="step3">SmurfSFCimTokenimTokenLogoSFC</p>
                </Step>
                <Step>
                    <p class="step1">2020/03/10 16:00</p>
                    <h3 class="step2">imTokenSFC</h3>
                    <p class="step3">SFCSmurf ERC20Smurf DAPPimToken</p>
                </Step>
            </Steps>
        </div>
        <div class="header">
            <div>
                <div>
                    <span>{{ $t('message.energyValue') }}</span>
                    <Progress
                      class="progress"
                      :percentage="percentage"
                      track-color="#0379E9"
                      color="#6CF1FF"
                    />
                </div>
                <Button @click="changeLanguage">{{ $t('message.language') }}</Button>
            </div>
            <span>{{ $t('message.totalIncome') }}{{ totalIncome }}ETH</span>
            <span>{{ $t('message.outAmount') }}{{ outAmount }}ETH</span>
        </div>
        <div class="bubble" v-lazy:background-image="!withdrawable ? require('../../assets/home-bg.png') : ''">
            <div v-if="!isBroken && isPause">
                <div class="bubble-big" :style="random(75, 85)" v-for="(item, index) of bubble.big" :key="item + index">
                    <img v-lazy="require('../../assets/bubble.png')">
                    <span>{{ item }}</span>
                </div>
                <div class="bubble-middle" :style="random(80, 90)" v-for="(item, index) of bubble.middle" :key="item + index">
                    <img v-lazy="require('../../assets/bubble.png')">
                    <span>{{ item }}</span>
                </div>
                <div class="bubble-small" :style="random(85, 92)" v-for="(item, index) of bubble.small" :key="item + index">
                    <img v-lazy="require('../../assets/bubble.png')">
                    <span>{{ item }}</span>
                </div>
            </div>
            <div v-if="isBroken && withdrawable">
                <water-polo :amount="`${redress}`"></water-polo>
            </div>
        </div>
        <div class="btn-else" v-if="isBroken">
            <Button @click="drawRedress" v-if="withdrawable">{{ $t('message.drawRemedy') }}</Button>
            <Button disabled v-else>{{ $t('message.drawRemedyDefine') }}</Button>
        </div>
        <div class="btn" v-else-if="isPause">
            <router-link :to="joinPath" v-if="isBind">{{ $t('message.join') }}</router-link>
            <Button disabled v-else>{{ $t('message.join') }}</Button>
            <i></i>
            <router-link to="/income" v-if="isBind">{{ $t('message.income') }}</router-link>
            <Button disabled v-else>{{ $t('message.income') }}</Button>
        </div>
        <div class="btn-else" v-else>
            <!-- <router-link to="/income"></router-link> -->
            <Button disabled>{{ $t('message.income') }}</Button>
        </div>
        <span class="title">{{ $t('message.statistics') }}</span>
        <div class="carousel">
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide">
                        <div>
                            <img src="../../assets/capital-pool.png" />
                        </div>
                        <span>{{ $t('message.capitalPool') }}/ETH</span>
                        <span>{{ amountPool }}</span>
                    </div>
                    <div class="swiper-slide">
                        <div>
                            <img src="../../assets/jackpot.png" />
                        </div>
                        <span>{{ $t('message.bigPool') }}/ETH</span>
                        <span>{{ luckAssetsPoolA }}</span>
                    </div>
                    <div class="swiper-slide">
                        <div>
                            <img src="../../assets/small-prize-pool.png" />
                        </div>
                        <span>{{ $t('message.smallPool') }}/ETH</span>
                        <span>{{ luckAssetsPoolB }}</span>
                    </div>
                    <div class="swiper-slide">
                        <div>
                            <img src="../../assets/cumulative-combustion.png" />
                        </div>
                        <span>{{ $t('message.totalBurn') }}/SFC</span>
                        <span>{{ erc20token }}</span>
                    </div>
                </div>
                <div class="swiper-pagination"></div>
            </div>
        </div>
        <span class="title">{{ $t('message.luckTime') }}</span>
        <div class="countdown">
            <count-down :time="time">
                <template v-slot="timeData">
                    <span class="item">{{ timeData.days < 10 ? 0 : `${timeData.days}`[0] }}</span>
                    <span
                        class="item"
                    >{{ timeData.days < 10 ? timeData.days : `${timeData.days}`[1] }}</span>
                    <span class="spl">:</span>
                    <span class="item">{{ timeData.hours < 10 ? 0 : `${timeData.hours}`[0] }}</span>
                    <span
                        class="item"
                    >{{ timeData.hours < 10 ? timeData.hours : `${timeData.hours}`[1] }}</span>
                    <span class="spl">:</span>
                    <span class="item">{{ timeData.minutes < 10 ? 0 : `${timeData.minutes}`[0] }}</span>
                    <span
                        class="item"
                    >{{ timeData.minutes < 10 ? timeData.minutes : `${timeData.minutes}`[1] }}</span>
                    <span class="spl">:</span>
                    <span class="item">{{ timeData.seconds < 10 ? 0 : `${timeData.seconds}`[0] }}</span>
                    <span
                        class="item"
                    >{{ timeData.seconds < 10 ? timeData.seconds : `${timeData.seconds}`[1] }}</span>
                </template>
            </count-down>
        </div>
        <div class="recordTitle">
            <span :class="isLatestJoin ? 'active' : ''" @click="isLatestJoin = true">{{ $t('message.newJoin') }}</span>
            <span :class="isLatestJoin ? '' : 'active'" @click="isLatestJoin = false">{{ $t('message.luckPlayer') }}</span>
        </div>
        <div class="record">
            <div class="record-header">
                <img v-lazy="require('../../assets/home-record.png')" />
            </div>
            <div class="record-content" v-show="isLatestJoin">
                <div v-for="(item, index) of newJoin" :key="index">
                    <span>{{ `${item.address.slice(0, 6)}****${item.address.slice(-6)}` }}</span>
                    <span>{{ item.amount }}ETH</span>
                    <span>{{ item.time }}</span>
                </div>
            </div>
			<div class="record-content" v-show="!isLatestJoin">
			    <div v-for="(item, index) of luckPlayer" :key="index">
			        <span>{{ `${item.address.slice(0, 6)}****${item.address.slice(-6)}` }}</span>
			        <span>{{ item.amount }}ETH</span>
			        <span>{{ item.time }}</span>
			    </div>
			</div>
        </div>
        <Overlay :show="show" @click="show = false">
            <div class="overlay" @click.stop>
                <div>
                    <span>{{ $t('message.welcome') }}</span>
                    <Icon class="icon" name="cross" @click="show = false" />
                </div>
                <input type="text" v-model="bindCode" autofocus :placeholder="$t('message.bind')" />
                <input type="text" v-model="generateCode" :placeholder="$t('message.createCode')" />
                <Button @click="bind">{{ $t('message.define') }}</Button>
            </div>
        </Overlay>
        <Overlay :show="isLuck" @click="isLuck = false">
            <div class="luck" @click.stop>
                <div>
                    <img v-lazy="require('../../assets/luck.png')">
                    <span>{{ luckAmount }} ETH</span>
                </div>
                <Button @click="getLuck">{{ $t('message.draw') }}</Button>
            </div>
        </Overlay>
        <Overlay :show="isWallet" @click="isWallet = false" class="notWallet-bg">
            <div class="notWallet" @click.stop>
                <div class="title">
                    <span></span>
                    <Icon @click="isWallet = false" class="icon" name="cross" />
                </div>
                <div class="text">
                    <i></i>
                    <span></span>
                </div>
                <div class="wallets">
                    <a href="https://www.token.im/?from=singlemessage">
                        <img v-lazy="require('../../assets/imToken.png')">
                        <span>imToken</span>
                    </a>
                    <a href="https://trustwallet.com/?from=singlemessage">
                        <img v-lazy="require('../../assets/trust.png')">
                        <span>Trust</span>
                    </a>
                    <a href="https://www.huobiwallet.com/?from=singlemessage">
                        <img v-lazy="require('../../assets/huobi.png')">
                        <span>Huobi</span>
                    </a>
                    <a href="http://mathwallet.net/cn/?from=singlemessage">
                        <img v-lazy="require('../../assets/mathwallet.png')">
                        <span>mathwallet</span>
                    </a>
                </div>
            </div>
        </Overlay>
    </div>
</template>

<script>
import Swiper from "swiper"
import "swiper/css/swiper.min.css"
import { NoticeBar, Progress, CountDown, Overlay, Icon, Toast, Button, Step, Steps} from "vant"
import WaterPolo from '@/components/water-polo'
import { guide  } from '../../static/initData'

export default {
    name: "home",
    components: { NoticeBar, Progress, CountDown, Overlay, Icon, Button, WaterPolo, Step, Steps },
    data() {
        return {
            show: false,
            percentage: 0,
            totalIncome: "0.00",
            outAmount: "0.00",
            joinPath: "/join",
            amountPool: "0.00",
            luckAssetsPoolA: "0.00",
            luckAssetsPoolB: "0.00",
            erc20token: "0.00",
            time: 0,
            bindCode: "",
            generateCode: "",
            bubble: { big: [], middle: [], small: [] },
            isLatestJoin: true,
            record: [],
            isLuck: false,
            luckAmount: '0.00',
            isPause: false,
            withdrawable: false,
            redress: '0.00',
            isBroken: false,
            newJoin: [],
            luckPlayer: [],
            isIOS: false,
            isNotic: true,
            isBind: false,
            isWallet: false
        };
    },
    methods: {
        noticeBarEvent() {
            this.isNotic = !this.isNotic
        },
        changeLanguage() {
            let language = localStorage.getItem('language')
            if (language === 'en-US') {
                this.$i18n.locale = 'zh-CN'
                localStorage.setItem('language', 'zh-CN')
            } else {
                this.$i18n.locale = 'en-US'
                localStorage.setItem('language', 'en-US')
            }
        },
        drawRedress() {
            this.$contract.round.methods.DrawRedress().send({
                from: this.$address
            }).on("error", console.error)
            .on("receipt", res => {
                this.withdrawable = false
                this.getData()
            })
        },
        getLuck() {
            this.$contract.luckAssetsPoolA.methods.WithdrawRewards().send({
                from: this.$address
            }).on("error", console.error)
            .on("receipt", res => {
                this.isLuck = false
                this.getData()
            })
        },
        random(top, left) {
            return {
                top: `${Math.random() * top}%`,
                left: `${Math.random() * left}%`
            }
        },
        async bind() {
            if (this.bindCode.length < 6) {
                return Toast.fail(this.$t('message.errBindLen'))
            } else if (this.generateCode.length !== 6) {
                return Toast.fail(this.$t('message.errCodeLen'))
            }

            let bindCode = this.$web3.utils.stringToHex(this.bindCode.toUpperCase()),
                generateCode = this.$web3.utils.stringToHex(this.generateCode.toUpperCase()),
                toAddress = await this.$contract.recommend.methods.ShortCodeToAddress(bindCode).call(),
                thisAddress = await this.$contract.recommend.methods.ShortCodeToAddress(generateCode).call()

            if (!parseInt(toAddress, 16)) {
                return Toast.fail(this.$t('message.errBind'))
            } else if (parseInt(thisAddress, 16)) {
                return Toast.fail(this.$t('message.errCode'))
            }

            this.$contract.round.methods.Activity(toAddress, generateCode).send({
                from: this.$address
            }).on("error", console.error)
            .on("receipt", res => {
                this.show = false
                this.isBind = true
                localStorage.setItem('isBind', 'true')
            })
        },
        init() {
            if (this.$cache.home) {
                this.percentage = this.$cache.home.percentage
                this.totalIncome = this.$cache.home.totalIncome
                this.outAmount = this.$cache.home.outAmount
                this.joinPath = this.$cache.home.joinPath
                this.amountPool = this.$cache.home.amountPool
                this.luckAssetsPoolA = this.$cache.home.luckAssetsPoolA
                this.luckAssetsPoolB = this.$cache.home.luckAssetsPoolB
                this.erc20token = this.$cache.home.erc20token
                this.time = this.$cache.home.time
                this.bubble = this.$cache.home.bubble
                this.withdrawable = this.$cache.home.withdrawable
                this.redress = this.$cache.home.redress
                this.isPause = this.$cache.home.isPause
                this.isBroken = this.$cache.home.isBroken
                
                // new Swiper(".swiper-container", {
                //     slidesPerView: 3,
                //     spaceBetween: 20,
                //     loop: true,
                //     pagination: {
                //       el: '.swiper-pagination'
                //     }
                // })
            } else {
                this.$cache.home = {}
            }

            this.getData()
        },
        smallBubble(income, obj) {
            let small1 = Math.floor(income / 0.05),
                small2 = Math.floor((income - small1 * 0.05) / 0.02),
                small3 = Math.floor((income - small1 * 0.05 - small2 * 0.02) / 0.01)

            for (let i = 0; i < small1; i++) {
                obj.small.push(0.05)
            }
            for (let i = 0; i < small2; i++) {
                obj.small.push(0.02)
            }
            for (let i = 0; i < small3; i++) {
                obj.small.push(0.01)
            }
            
            return obj
        },
        middleBubble(income, obj) {
            let middle1 = Math.floor(income / 0.5),
                middle2 = Math.floor((income - middle1 * 0.5) / 0.1)

            for (let i = 0; i < middle1; i++) {
                obj.middle.push(0.5)
            }
            for (let i = 0; i < middle2; i++) {
                obj.middle.push(0.1)
            }
            
            return this.smallBubble((income - middle1 * 0.5 - middle2 * 0.1), obj)
        },
        initBubble(income, obj) {
            let ten = Math.floor(income / 10),
                five = Math.floor((income - ten * 10) / 5),
                one = Math.floor((income - ten * 10 - five * 5) / 1)

            for (let i = 0; i < ten; i++) {
                obj.big.push('10.00')
            }
            for (let i = 0; i < five; i++) {
                obj.big.push('5.00')
            }
            for (let i = 0; i < one; i++) {
                obj.big.push('1.00')
            }

            return this.middleBubble((income - ten * 10 - five * 5 - one * 1), obj)
        },
        async getData() {
            this.$contract.round.methods.isBroken().call((err, res) => {
                if (!err) {
                    this.isBroken = res
                    this.$cache.home.isBroken = res
                }
            })

            this.$contract.round.methods.GetRedressInfo()
            .call({ from: this.$address }, (err, res) => {
                if (!err) {
                    this.redress = this.$fromWei(res.total)
                    this.withdrawable = res.withdrawable

                    this.$cache.home.redress = this.redress
                    this.$cache.home.withdrawable = this.withdrawable
                }
            })

            this.$contract.luckAssetsPoolA.methods.RewardsAmount()
            .call({ from: this.$address }, (err, res) => {
                if (!err) {
                    this.luckAmount = this.$fromWei(res)
                    this.isLuck = this.luckAmount > 0
                }
            })

            this.$contract.luckAssetsPoolA.methods._needPauseGame().call((err, res) => {
                if (!err) {
                    this.isPause = !res
                    this.$cache.home.isPause = !res
                }
            })

            this.$contract.round.methods.CurrentCanWithdrawProfix(this.$address).call((err, res) => {
                if (!err) {
                    let st = this.$fromWei(res.st),
                        dy = this.$fromWei(res.dy),
                        canWithdraw = st + dy

                    if (canWithdraw > Number(this.outAmount)) {
                        canWithdraw = Number(this.outAmount)
                    }

                    this.bubble = this.initBubble(canWithdraw, { big: [], middle: [], small: [] })
                    this.$cache.home.bubble = this.bubble
                }
            })

            this.$contract.recommend.methods.GetIntroducer(this.$address)
            .call((err, res) => {
                if (!err) {
                    this.show = !Number(res)
                    this.isBind = Number(res)
                    if (Number(res) != 0) {
                        localStorage.setItem('isBind', 'true')
                    }
                }
            })

            this.$contract.turing.methods.GetSafetyValue().call((err, res) => {
                if (!err) {
                    this.percentage = Number(res)
                    this.$cache.home.percentage = Number(res)
                }
            })

            this.$contract.round.methods.TotalInOutAmount()
            .call({ from: this.$address }, (err, res) => {
                if (!err) {
                    this.totalIncome = this.$fromWei(res.outEther, 2)
                    this.$cache.home.totalIncome = this.$fromWei(res.outEther, 2)
                }
            })

            this.$contract.round.methods.GetCurrentGameStatus()
            .call({ from: this.$address }, (err, res) => {
                if (!err) {
                    let outAmount = this.$fromWei(res.currentEther) * Number(res.profixMultiplier) - this.$fromWei(res.withdrawableTotal)
                    this.outAmount = outAmount.toFixed(2)
                    this.$cache.home.outAmount = outAmount.toFixed(2)
                    this.joinPath = res.createTime === "0" ? "/join" : "/againJoin"
                    this.$cache.home.joinPath = this.joinPath
                }
            })

            this.$web3.eth.getBalance(this.$contract.round._address).then(balance => {
                this.amountPool = this.$fromWei(balance, 2)
                this.$cache.home.amountPool = this.$fromWei(balance, 2)
            })

            this.$web3.eth.getBalance(this.$contract.luckAssetsPoolA._address).then(balance => {
                this.luckAssetsPoolA = this.$fromWei(balance, 2)
                this.$cache.home.luckAssetsPoolA = this.$fromWei(balance, 2)
            })

            this.$web3.eth.getBalance(this.$contract.luckAssetsPoolB._address).then(balance => {
                this.luckAssetsPoolB = this.$fromWei(balance, 2)
                this.$cache.home.luckAssetsPoolB = this.$fromWei(balance, 2)
            })

            this.$contract.erc20token.methods.balanceOf("0x0000000000000000000000000000000000000000").call((err, res) => {
                if (!err) {
                    this.erc20token = this.$fromWei(res, 2)
                    this.$cache.home.erc20token = this.$fromWei(res, 2)
                }

                new Swiper(".swiper-container", {
                    slidesPerView: 3,
                    spaceBetween: 10,
                    loop: true,
                    pagination: {
                      el: '.swiper-pagination'
                    }
                })
            })

            this.$contract.luckAssetsPoolA.methods._lotteryTime().call((err, res) => {
                if (!err) {
                    this.time = new Date(Number(res) * 1000).getTime() - new Date().getTime()
                    this.$cache.home.time = this.time
                }
            })

            let block = await this.$web3.eth.getBlockNumber().then()

            this.$contract.roundWS.events.Log_NewDeposited({
              fromBlock: block - 10800
            }).on('data', res => {
				let time = new Date(Number(res.returnValues.time) * 1000),
					year = time.getFullYear(),
					month = time.getMonth() + 1,
					day = time.getDate()
				
				this.newJoin.push({
					address: res.returnValues.owner,
					amount: this.$fromWei(res.returnValues.value),
					time: `${year}.${month}.${day}`
				})
            }).on('error', console.error)
			
            this.$contract.luckAssetsPoolBWS.events.Log_Winner({
              fromBlock: block - 10800
            }).on('data', res => {
              let time = new Date(Number(res.returnValues.when) * 1000),
				  year = time.getFullYear(),
              	  month = time.getMonth() + 1,
              	  day = time.getDate()
              
              this.luckPlayer.push({
              	address: res.returnValues.owner,
              	amount: this.$fromWei(res.returnValues.amount),
              	time: `${year}.${month}.${day}`
              })
            }).on('error', console.error)
        }
    },
    mounted() {
        this.isWallet = !guide

        let userAgent = navigator.userAgent,
            isIOS = !!userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);

        if (isIOS) {
            let pagination = document.querySelector('.swiper-pagination'),
                title = document.querySelector('.countdown')
            
            pagination.style.bottom = '-1.5rem'
            title.style.marginTop = '.3rem'
        }

        let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale
        if (guide) {
            this.init()
        }
    }
};
</script>

<style lang="scss">
.progress > span > span {
    height: 22px;
    line-height: 22px;
}
.swiper-container {
    overflow: visible;
    --swiper-theme-color: #0083F1;
}
.swiper-pagination {
    bottom: -45px !important;
    &>.swiper-pagination-bullet {
        width: 15px;
        height: 15px;
        background-color: #6654E6;
    }
    &>.swiper-pagination-bullet-active {
        background-color: #0083F1;
    }
}
.van-step {
    color: #D1D1D1;
}
.van-icon-stop {
    color: #0083F1 !important;
    font-size: 24px;
}
.van-step--vertical {
    line-height: normal;
}
.van-step__line, .van-step--vertical:first-child::before {
    background-color: #666666;
}
.van-step:last-child .van-step__line {
    width: 1px;
}
.van-step--vertical:not(:last-child)::after {
    border: none;
}
</style>

<style lang="scss" scoped>
.notWallet-bg {
    padding: 20vh 20px 0;
}
.notWallet {
    color: #fff;
    padding: 30px 22px 50px 14px;
    background-color: #441EA8;
    &>.title {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        &>span {
            font-size: 40px;
            font-weight: 400;
        }
        &>.icon {
            position: absolute;
            right: 22px;
            font-size: 40px;
            float: right;
        }
    }
    &>.text {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin: 20px 0;
        &>i {
            display: inline-block;
            width: 10px;
            height: 36px;
            background-color: #098DE6;
            margin-right: 12px;
        }
        &>span {
            font-size: 32px;
        }
    }
    &>.wallets {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        &>a {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: calc(100% / 4);
            color: #fff;
            font-size: 28px;
        }
        &>a:nth-child(1) {
            &>img {
                width: 70px;
                height: 50px;
                margin-bottom: 18px;
            }
        }
        &>a:nth-child(2) {
            &>img {
                width: 80px;
                height: 80px;
                margin-bottom: 2px;
            }
        }
        &>a:nth-child(3) {
            &>img {
                width: 44px;
                height: 68px;
                margin-bottom: 10px;
            }
        }
