<template>
    <div class="box">
        <again-join
         :currentJoin="current"
         :title="`${$t('message.canDraw')}/SFC`"
         :gridList="gridList"
         :btnData="btnData"
        ></again-join>
    </div>
</template>

<script>
import AgainJoin from '@/components/again-join.vue'

export default {
    components: { AgainJoin },
    data() {
        return {
            current: '0.00',
            gridList: [
                { icon: 'compensate-total', text: this.$t('message.totalRemedy'), amount: 8546, unit: 'SFC' },
                { icon: 'await-freed', text: this.$t('message.awaitFreed'), amount: 8546, unit: 'SFC' },
                { icon: 'everyday-freed', text: this.$t('message.everydayFreed'), amount: 8546, unit: 'SFC' },
                { icon: 'income-record', text: this.$t('message.drawRecord'), routerText: this.$t('message.details'), routerPath: '/my/view/compensateRecord' }
            ],
            btnData: { text: this.$t('message.draw'), disabled: true }
        }
    },
    methods: {
        withdraw() {
            this.$contract.redress.methods.WithdrawRedress().send({
                from: this.$address
            }).on("transactionHash", txHash => {
                console.log(txHash)
            }).on("error", console.error)
            .on("receipt", res => {
                this.getData()
            })
        },
        init() {
            if (this.$cache.compensate) {
                this.current = this.$cache.compensate.current
                this.gridList[0].amount = this.$cache.compensate.total
                this.gridList[1].amount = this.$cache.compensate.wait
                this.gridList[2].amount = this.$cache.compensate.everyday
            } else {
                this.$cache.compensate = {}
            }

            this.getData()
        },
        getData() {
            this.$contract.redress.methods._withdrawProp().call((err, result) => {
                if (!err) {
                    let prop = Number(result / 100)

                    this.$contract.redress.methods.RedressInfo()
                    .call({ from: this.$address }, (err, res) => {
                        if (!err) {
                            this.current = `${this.$fromWei(res.cur, 2)}`
                            this.gridList[0].amount = `${this.$fromWei(res.total, 2)}`
                            this.gridList[1].amount = `${(this.$fromWei(res.total) - this.$fromWei(res.withdrawed)).toFixed(2)}`
                            this.gridList[2].amount = `${(this.$fromWei(res.total) * prop).toFixed(2)}`

                            this.$cache.compensate.current = this.current
                            this.$cache.compensate.total = this.gridList[0].amount
                            this.$cache.compensate.wait = this.gridList[1].amount
                            this.$cache.compensate.everyday = this.gridList[2].amount

                            this.btnData.disabled = !(this.current > 0)
                        }
                    })
                }
            })
        }
    },
    mounted() {
        let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale
        this.init()
    }
}
</script>

