<template>
  <tabbar
   v-if="$route.meta.tabbar"
   v-model="active"
   active-color="#0083F1"
   inactive-color="#B5B5B5"
   router
  >
    <tabbar-item icon="wap-home-o" name="home" to="/">{{ $t('message.home') }}</tabbar-item>
    <tabbar-item icon="search" name="change" to="/change">
      <span>{{ $t('message.change') }}</span>
      <self-icon slot="icon" type="change"></self-icon>
    </tabbar-item>
    <tabbar-item icon="friends-o" name="my" to="/my">
      <span>{{ $t('message.my') }}</span>
      <self-icon slot="icon" type="my"></self-icon>
    </tabbar-item>
  </tabbar>
</template>

<script>
import { Tabbar, TabbarItem } from 'vant'

export default {
  name: 'self-tabbar',
  components: { Tabbar, TabbarItem },
  data() {
    return {
      active: 'home'
    }
  },
  mounted() {
    this.active = this.$route.name
  }
}
</script>

<style lang="scss" scoped>
.van-iconfont {
  font-size: 25px !important;
}
</style>

<style lang="scss">
.van-tabbar .van-icon {
  font-size: 25px !important;
}
</style>