<template>
    <div class="box">
        <div class="header">
            <span :class="type ? 'active' : ''" @click="type = true">{{ $t('message.joinRecord') }}</span>
            <span :class="type ? '' : 'active'" @click="type = false">{{ $t('message.incomeRecord') }}</span>
        </div>
        <join-record v-show="type"></join-record>
        <record-list v-show="!type" :list="list"></record-list>
    </div>
</template>

<script>
import JoinRecord from './JoinRecord'
import RecordList from '@/components/record-list'

export default {
    components: { JoinRecord, RecordList },
    data() {
        return {
            type: true,
            list: []
        }
    },
    mounted() {
        let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale

        if (this.$route.query.type === '') {
            this.type = false
        }

        this.$contract.roundWS.events.Log_ProfixHistory({
            filter: { owner: this.$address },
            fromBlock: 9130000
        }).on('data', res => {
            let type = Number(res.returnValues.ptype),
                time = new Date(Number(res.returnValues.time) * 1000),
                year = time.getFullYear(),
                month = time.getMonth() + 1,
                day = time.getDate()
                
            if (type >= 0 && type < 15) {
                type = this.$t('message.sharingAward')
            } else if (type > 20 && type < 25) {
                type = `V${res.returnValues.ptype.substr(1, 1)}`
            } else if (type > 30 && type < 35) {
                type = this.$t('message.tieAward')
            } else if (type === 40) {
                type = this.$t('message.dividend')
            }
            
            this.list.push({
                type: type,
                amount: this.$fromWei(res.returnValues.value),
                time: `${year}/${month}/${day}`
            })
        }).on('error', console.error)
    }
}
</script>

<style lang="scss" scoped>
.header {
    width: 100%;
    height: 118px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-bottom: 2px solid #C4C4C4;
    &>span {
        color: #8559F9;
        font-size: 28px;
        font-weight: bold;
        padding: 12px 0;
    }
    &>.active {
        color: #fff;
        font-size: 32px;
        border-bottom: 4px solid #0083F1;
    }
}
</style>