<template>
    <div>
        <div class="header">
            <Row>
                <Col span="12">{{ $t('message.joinAmount') }}</Col>
                <Col span="12">{{ $t('message.joinTime') }}</Col>
            </Row>
        </div>
        <div class="content">
            <Row class="row" v-for="(item, index) of list" :key="index">
                <Col span="12">{{ item.amount }} ETH</Col>
                <Col span="12">{{ item.time }}</Col>
            </Row>
        </div>
    </div>
</template>

<script>
import { Row, Col } from 'vant'

export default {
    components: { Row, Col },
    data() {
        return {
            list: []
        }
    },
    methods: {
        init() {
            this.$contract.roundWS.events.Log_NewDeposited({
                filter: { owner: this.$address },
                fromBlock: 9130000
            }).on('data', res => {
				let time = new Date(Number(res.returnValues.time) * 1000),
					year = time.getFullYear(),
					month = time.getMonth() + 1,
					day = time.getDate()
					
                this.list.push({
					amount: this.$fromWei(res.returnValues.value),
					time: `${year}/${month}/${day}`
				})
            }).on('error', console.error)
        }
    },
    mounted() {
        let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale
        this.init()
    }
}
</script>

<style lang="scss" scoped>
.content {
    width: 100%;
    height: calc(100vh - 184px);
    color: #fff;
    font-size: 20px;
    overflow-y: auto;
    &>div {
        margin-top: 20px;
    }
}
.header {
    width: 100%;
    height: 66px;
    color: #fff;
    font-size: 24px;
    line-height: 66px;
    border-bottom: 2px solid #C4C4C4;
}
</style>