<template>
    <div class="box">
        <again-join
         :currentJoin="currentJoin"
         :title="`${this.$t('message.currentJoin')}/ETH`"
         :gridList="gridList"
         :btnData="btnData"
        ></again-join>
        <span class="toast" v-if="this.btnData.disabled">48!</span>
    </div>
</template>

<script>
import AgainJoin from '@/components/again-join.vue'

export default {
    components: { AgainJoin },
    data() {
        return {
            currentJoin: '0.00',
            gridList: [
                { icon: 'out-balance', text: this.$t('message.outAmount'), amount: '60.00', unit: 'ETH' },
                { icon: 'investable-balance', text: this.$t('message.canJoinBalance'), amount: '60.00', unit: 'ETH' },
                { icon: 'outline', text: this.$t('message.totalJoin'), amount: '60.00', unit: 'ETH' },
                { icon: 'income-record', text: this.$t('message.joinRecord'), routerText: this.$t('message.details'), routerPath: '/record?type=' }
            ],
            btnData: { text: this.$t('message.againJoin'), path: '/join', disabled: true }
        }
    },
    methods: {
        init() {
            if (this.$cache.againJoin) {
                this.currentJoin = this.$cache.againJoin.currentJoin
                this.gridList[0].amount = this.$cache.againJoin.outAmount
                this.gridList[1].amount = this.$cache.againJoin.canJoin
                this.gridList[2].amount = this.$cache.againJoin.totalJoin
                this.btnData.disabled = this.$cache.againJoin.disabled
            } else {
                this.$cache.againJoin = {}
            }

            this.getData()
        },
        getData() {
            this.$contract.round.methods.GetCurrentGameStatus()
            .call({ from: this.$address }, (err, res) => {
                if (!err) {
                    let currentJoin = this.$fromWei(res.currentEther),
                        canJoin = this.$fromWei(res.depositMaxLimit) - currentJoin,
                        outAmount = currentJoin * Number(res.profixMultiplier) - this.$fromWei(res.withdrawableTotal),
                        // latestJoin = new Date(Number(res.latestDepositInTime) * 1000 + 3600000).getTime()
                        latestJoin = new Date(Number(res.latestDepositInTime) * 1000 + (48 * 3600000)).getTime()

                    this.currentJoin = `${currentJoin.toFixed(2)}`
                    this.gridList[0].amount = `${outAmount.toFixed(2)}`
                    this.gridList[1].amount = `${canJoin.toFixed(2)}`

                    this.$cache.againJoin.currentJoin = `${currentJoin.toFixed(2)}`
                    this.$cache.againJoin.outAmount = `${outAmount.toFixed(2)}`
                    this.$cache.againJoin.canJoin = `${canJoin.toFixed(2)}`

                    if (canJoin > 0 && new Date().getTime() >= latestJoin) {
                        this.btnData.disabled = false
                    } else {
                        this.btnData.disabled = true
                    }
                    this.$cache.againJoin.disabled = this.btnData.disabled
                }
            })

            this.$contract.round.methods.TotalInOutAmount()
            .call({ from: this.$address }, (error, result) => {
                if (!error) {
                    let inEther = this.$fromWei(result.inEther)

                    this.gridList[2].amount = `${inEther.toFixed(2)}`
                    this.$cache.againJoin.totalJoin = this.gridList[2].amount
                }
            })
        }
    },
    mounted() {
        let lang = localStorage.getItem('language')
        this.$i18n.locale = lang ? lang : this.$i18n.locale
        this.init()
    }
}
</script>

<style lang="scss" scoped>
.toast {
    color: #666;
    font-size: 25px;
}
</style>