<template>
    <div class="box">
        <div class="header">
            <div id="chart"></div>
            <span>{{ title }}</span>
        </div>
        <div class="content">
            <Grid :border="false" :gutter="1" :column-num="2">
                <grid-item
                 class="content-item"
                 v-for="(item, index) of gridList"
                 :key="index"
                >
                    <self-icon class="icon" :type="item.icon"></self-icon>
                    <span>{{ item.text }}</span>
                    <span v-if="item.amount">{{ item.amount }}{{ item.unit }}</span>
                    <router-link v-else :to="item.routerPath">{{ item.routerText }}</router-link>
                </grid-item>
            </Grid>
        </div>
        <div class="footer">
            <router-link
             v-if="btnData.path && !btnData.disabled"
             :to="btnData.path"
            >{{ btnData.text }}</router-link>
            <Button
             v-else-if="btnData.path"
             disabled
            >{{ btnData.text }}</Button>
            <Button v-else-if="btnData.text && !btnData.disabled">{{ btnData.text }}</Button>
            <Button v-else disabled>{{ btnData.text }}</Button>
        </div>
    </div>
</template>

<script>
import echarts from 'echarts'
import '@/components/echarts-liquidfill.min'
import { Grid, GridItem, Button } from 'vant'

export default {
    components: { Grid, GridItem, Button },
    props: {
        currentJoin: String,
        title: String,
        gridList: Array,
        btnData: Object
    },
    data() {
        return {
            WaterPolo: ''
        }
    },
    mounted() {
        let _this = this
        this.WaterPolo = echarts.init(document.getElementById('chart'))
        this.WaterPolo.setOption({
            series: [{
                type: 'liquidFill',
                radius: '100%',
                data: [{
                    value: 0.5,
                    direction: 'left',
                    itemStyle: {
                        normal: {
                            color: '#40AFFF'
                        }
                    }
                }, {
                    value: 0.5,
                    direction: 'right',
                    itemStyle: {
                        normal: {
                            color: '#40AFFF'
                        }
                    }
                }],
                backgroundStyle: {
                    borderWidth: 1,
                    borderColor: '#40AFFF',
                    color: '#fff'
                },
                label: {
                    normal: {
                        formatter: _this.currentJoin,
                        textStyle: {
                            fontSize: 20
                        }
                    }
                },
                outline: {
                    show: false
                }
            }]
        })
    },
    watch: {
        currentJoin(val) {
            this.WaterPolo.setOption({
                series: [{
                    label: {
                        normal: {
                            formatter: val
                        }
                    }
                }]
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.footer {
    width: 340px;
    height: 72px;
    margin: 60px auto 0;
    &>a {
        display: inline-block;
        width: 100%;
        height: 100%;
        color: #fff;
        border-radius: 8px;
        font-size: 32px;
        background-color: #0083F1;
        line-height: 72px;
    }
    &>button {
        display: inline-block;
        border: none;
        outline: none;
        width: 100%;
        height: 100%;
        color: #fff;
        border-radius: 8px;
        font-size: 32px;
        background-color: #0083F1;
        line-height: 72px;
    }
}
.content {
    width: 670px;
    height: 500px;
    margin: 0 auto;
    border-radius: 20px;
    overflow: hidden;
    &>.van-grid {
        width: 100%;
        height: 100%;
    }
    .content-item {
        .icon {
            font-size: 60px;
            color: #0083F1;
        }
        span:nth-of-type(1) {
            font-size: 32px;
            font-weight: 400;
        }
        span:nth-of-type(2) {
            font-size: 40px;
            font-weight: 400;
        }
        a {
            font-size: 32px;
            color: #0083F1;
            text-decoration: underline;
        }
    }
}
.header {
    width: 240px;
    margin: 0 auto;
    padding: 60px 0 40px;
    &>#chart {
        width: 240px;
        height: 240px;
        // border-radius: 50%;
        // overflow: hidden;
        overflow: visible;
    }
    &>span {
        font-size: 28px;
        color: #fff;
    }
}
</style>